.wrapper{
    background-color: #fff;
    width: 100%;
    height: 100vh;
    /* overflow: hidden; */
}
.header_container{
    background-color: gray;
    width: 100%;
    height: 60px;
    border-radius: 10px;
    display: flex;
}
.avatar{
    border-radius: 40px;
    background-color: #fff;
    width: 50px;
    height: 50px;
    margin-left: 10px;
}
.username{
    font-size: 20px;
    margin-left: 5px;
}
.online_status{
    font-size: bold;
    font-style: italic;
    font-size: 20px;
}
.input_container{
    width: 80%;
    height: 50px;
    background-color: rgb(236, 228, 228);
    border-radius: 10px;

}
.input{
    width: 95%;
    height: 45px;
    border:2px solid grey;
    border-radius: 10px;
    margin-left: 5px;
}
.chat_container{
  height: 500px;
  width: 100%;
  overflow: auto;
}
.footer_container{
    display: flex;
}
.button{
margin-left: 15px;
    border-radius: 20px;
    width: 50px;
    background-color: grey;
    font-weight: bold;
}
.button:hover{
    cursor: pointer;
    background-color: rgb(250, 243, 243);
}
